<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  name: 'batchUploadImg',
  props: {
    /**
     * 配置参数获取
     * url 上传地址
     * type 上传类型限制用|隔开 默认jpg|jpeg|png
     * maxSize 单文件最大上传大小(M) 默认50M
     * maxNum 单次文件最大上传数量
     * watermark 水印需求 NO无 RB右边 LB左边
     * sort  1截图上传 2编辑器上传
    */
    batchUploadImgConfig: { type: Object}
  },
  data () {
    return {
      waitUploadImgFileList: [],
      waitUploadImgPreviewList: [],
      uploadList: [],
      uploadImgMsg: {},
      uploadType: '',
    }
  },
  mounted () {
    this.uploadType = this.batchUploadImgConfig.sort
    // console.log(this.uploadType)
    // console.log(this.batchUploadImgConfig)
  },
  methods: {
    handleRemove (i) {
      // console.log(i)
      this.waitUploadImgFileList.splice(i, 1)
      this.waitUploadImgPreviewList.splice(i, 1)
      this.setImageMsg()
      // console.log(this.waitUploadImgFileList, this.waitUploadImgPreviewList)
    },//预览图片删除
    handleBeforeUpload (file) {
      // console.log(this.batchUploadImgConfig)
      const num = this.batchUploadImgConfig.maxNum || 300,
            size = file.size/1048576,
            type = file.type,
            limitSize = this.batchUploadImgConfig.maxSize || 50,
            limitType = this.batchUploadImgConfig.type || 'jpg|jpeg|png',
            limitTypeReg = new RegExp('.('+limitType+')$'),
            name = file.name;
      
      //单次上传数量校验
      let curNum = 0;
      this.waitUploadImgFileList.forEach(ele => {
        ele.status == 2 ? ++curNum : null
      })
      const limitNum = curNum < num;

      //重复图片校验
      let isRepeatImg =  this.waitUploadImgPreviewList.some(ele => {
        return name == ele.name
      })
      
      if (isRepeatImg) {
        this.$Message.warning('图片已存在请勿重复添加')
      } else if (!limitNum) {
        this.$Message.warning(`单次上传最多可选${num}张`)
      } else if (!limitTypeReg.test(type)) {
        this.$Message.warning(`请传${limitType}图片`)
      } else if (size > limitSize ) {
        this.$Message.warning(`单张图片不能超过${limitSize}M`)
      } else {
        let fr = new FileReader();
        fr.onload = () => {
          //status 1已上传 2为待上传
          this.waitUploadImgFileList.push({file,status: 2});
          //uploaded 0未上传 1成功 2失败 uploading 0非正在上传状态 1正在上传 percent 上传进度
          this.waitUploadImgPreviewList.push({
            name, 
            url: fr.result, 
            uploaded: 0, 
            uploading: 0, 
            percent:0, 
            size: size.toFixed(3),
            trueUrl: ''
          })
        };
        fr.readAsDataURL(file)
        this.setImageMsg()
      }
        
      // console.log(this.waitUploadImgFileList, this.waitUploadImgPreviewList)
      return false;
    },// 图片上传预处理
    startHandleUpload () {
      this.waitUploadImgFileList.forEach((ele,index) => {
        // console.log(ele, index)
        if(ele.status === 2) {
          this.handleUpload(ele.file, index)
        }
      })
    },// 图片上传执行
    handleUpload (files, index) {
      this.waitUploadImgPreviewList[index].uploading = 1;
      
      const timer = setInterval(() => {
        if(this.waitUploadImgPreviewList[index].percent < 60) {
          this.waitUploadImgPreviewList[index].percent+=10
        } else if (this.waitUploadImgPreviewList[index].percent < 98) {
          this.waitUploadImgPreviewList[index].percent+=2
        } else {
          clearInterval(timer)
        }
      }, 100)
      
      const token = getToken(),
            file = files,
            imgFile = files,
            water =  this.batchUploadImgConfig.watermark || '',
            uploadUrl = this.batchUploadImgConfig.url;

      let formData = new FormData();
          formData.append('token',  token);
      water ? (formData.append('imgFile',imgFile),formData.append('water', water)) : formData.append('file',file);
      let params = formData;
      
      api[uploadUrl](params).then(res => {
        clearInterval(timer)
        if(res.code == 0) {
          this.waitUploadImgFileList[index].status = 1;
          this.waitUploadImgPreviewList[index].uploaded = 1;
          this.waitUploadImgPreviewList[index].uploading = 0;
          this.waitUploadImgPreviewList[index].percent = 100;
          
          this.setImageMsg();

          let type = res.data ? 1 : 2; //1截图上传返回 2编辑器上传返回
          // console.log(type)
          this.uploadType = type;
          if(type == 1) {
            this.uploadList.push(res.data.path)
            this.waitUploadImgPreviewList[index].trueUrl = res.data.path
          } else {
            this.uploadList.push(res.url)
            this.waitUploadImgPreviewList[index].trueUrl = res.url
          }
        } else {
          this.waitUploadImgPreviewList[index].uploaded = 2;
          this.waitUploadImgPreviewList[index].uploading = 0;
          this.$Message.error(res.msg)
        }
        // console.log(this.waitUploadImgPreviewList)
      }).catch(err => {
        this.waitUploadImgPreviewList[index].uploaded = 2;
        this.waitUploadImgPreviewList[index].uploading = 0;
        this.$Message.error(err)
      })
    },// 图片上传执行函数
    setImageMsg () {
      let imgSizeSum = 0;
      let uploadImgSize = 0;
      let uploadImgNum = 0;
      this.uploadImgMsg.waitUplodNum = this.waitUploadImgFileList.length;
      this.waitUploadImgFileList.forEach(ele => {
        imgSizeSum+=(ele.file.size/1048576)
        if(ele.status == 1) {
          uploadImgSize+=(ele.file.size/1048576)
          uploadImgNum+=1
        }
      })
      this.uploadImgMsg.imgSizeSum = imgSizeSum.toFixed(3)
      this.uploadImgMsg.uploadImgSize = uploadImgSize.toFixed(3)
      this.uploadImgMsg.uploadImgNum = uploadImgNum
      this.uploadImgMsg.imgNum = this.waitUploadImgFileList.length
    },//采集图片数据
    clearAllImage () {
      this.waitUploadImgFileList = []
      this.waitUploadImgPreviewList = []
      this.uploadList = []
      this.uploadImgMsg = {}
      this.$Message.success('清除成功')
    },//清除所有图片
    insertAllImage () {
      this.$Message.success('插入成功')
    },//插入所有已上传图片
    insertSingleImage (url) {
      this.$emit('insertSingleImage', url)
    }//插入指定已上传图片
  }
}
</script>
<template>
  <div>
    <template v-if="waitUploadImgFileList.length>0">
      <div class="uploadlistBox">
        <div class="upload-list" v-for="item,idx in waitUploadImgPreviewList" :key="idx">
          <Progress 
            class="processBox" 
            v-if="item.uploading" 
            :percent="item.percent" 
            hide-info 
            status="active"
            :stroke-color="['#108ee9', '#87d068']"
            >
          </Progress>
          <img :src="item.url">
          <Icon v-show="item.uploaded == 1" type="md-checkmark-circle-outline" class="uploadedIcon" />
          <Icon v-show="item.uploaded == 2" type="md-close-circle" class="uploadedIconError" />
          <div class="upload-list-cover" :class="{halfH: uploadType == 2 && item.uploaded == 1}">
            <div 
              v-if="uploadType == 2 && item.uploaded == 1"
              @click="insertSingleImage(item.trueUrl)"
              >
              <Icon type="md-code-download" />
              <span style="color:#fff;position: relative;top:1px">插入</span>
            </div>
            <div @click="handleRemove(idx)">
              <Icon type="ios-trash-outline"></Icon>
              <span style="color:#fff;position: relative;top:3px">{{item.size}}MB</span>
            </div>
          </div>
        </div>
        <Upload
          ref="upload"
          :show-upload-list="false"
          :before-upload="handleBeforeUpload"
          multiple
          type="drag"
          action=""
          style="display: inline-block;width:142px;">
            <div style="width: 142px;height:142px;line-height: 142px;">
              <Icon type="ios-camera" size="20"></Icon>
          </div>
        </Upload>
      </div>
      <div class="uploadMsgBox">
        <Button type="info" style="margin-top:10px" @click="startHandleUpload">开始上传</Button>
        <div>当前选中<span> {{uploadImgMsg.imgNum}} </span>张图 | 总计<span> {{uploadImgMsg.imgSizeSum}} </span>MB | 已上传<span> {{uploadImgMsg.uploadImgNum}} </span>张图 | 共消耗<span> {{uploadImgMsg.uploadImgSize}} </span>MB流量</div>
      </div>
    </template>
    <template v-else>
      <Upload
        ref="upload"
        multiple
        type="drag"
        :before-upload="handleBeforeUpload"
        action=""
        >
        <div style="padding: 60px 0">
            <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
            <p>点击或将文件拖拽到这里上传</p>
        </div>
      </Upload>
    </template>
  </div>
</template>
<style lang="scss" scoped>
  .uploadlistBox{
      background: #fff;
      border: 1px dashed #dcdee2;
      border-radius: 4px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
      transition: border-color .2s ease;
      padding: 20px 20px 20px;
      &:hover {
        border: 1px dashed #2d8cf0;
      }
      .upload-list{
        display: inline-block;
        height: 144px;
        line-height: 144px;
        border-radius: 4px;
        overflow: hidden;
        position: relative;
        margin-right: 6px;
        text-align: center;
        box-shadow: 0 1px 1px #00000020;
        &:hover .upload-list-cover {
          display: block;
        }
        img{
          width: 100%;
          height: 100%;
        }
        .upload-list-cover{
          display: none;
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          background: rgba(0,0,0,.6);
          &.halfH {
            line-height: 72px;
            div:nth-child(1) {
              &:hover {
                background: #34d6177a;
              }
            }
            div:nth-child(2) {
              &:hover {
                background: #d61717a3;
              }
            }
          }
          i{
            color: #fff;
            font-size: 20px;
            cursor: pointer;
            margin: 0 2px;
          }
        }
      }
      .uploadedIcon, .uploadedIconError {
        position: absolute;
        bottom: 2px;
        right: 6px;
        font-size: 26px;
        color: #fff;
        z-index: 1;
        &::after {
          content: '';
          position: absolute;
          top: -32px;
          left: -28px;
          border-width: 30px;
          border-style: solid;
          border-radius: 0 0 4px 0;
          border-color: transparent green green transparent;
          z-index: -1;
        }
      }
      .uploadedIconError {
        &::after {
          border-color: transparent red red transparent;
        }
      }
      .processBox {
        position: absolute;
        top: 0;
        left: 0;
        background: #0009;
        padding: 2px;
      }
  }
  .uploadMsgBox {
    display: flex;
    align-items: center;
    div {
      font-size: 14px;
      margin: 10px 0 0 10px;
      span {
        color: red;
        font-weight: bold;
      }
    }
  }
</style>