<script>
import api from '@/api'
import { getToken } from '@/api/auth'
import E from 'wangeditor'
import BatchUploadImg from '@/components/BatchUploadImg'

export default {
  props: {
    getEditorContent:{
      type: String,
      default: ''
    },
    getEditorType: {
      type: Number
    },
    getEditorId: {
      type: String
    },
  },
  data () {
    return {
      uploadImgBox: false,
      batchUploadImgConfig: {
        url: 'dirImage',
        maxSize: 5,
        maxNum: 20,
        type: '',
        watermark: 'NO',
        sort: 2,
        insertSingleUrl: '',
        editor: {}
      },
      isShowHtml: false,
      htmlCode: '',
    }
  },
  mounted () {
    const _this = this;
    // 获取必要的变量，这些在下文中都会用到
    const { BtnMenu } = E;
    // 第一，菜单 class ，Button 菜单继承 BtnMenu class
    class BatchUpload extends BtnMenu {
      constructor(editor) {
        // data-title属性表示当鼠标悬停在该按钮上时提示该按钮的功能简述
        const $elem = E.$(
          `<div class="w-e-menu" data-title="批量上传">
            <svg t="1637812787736" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5310" width="16" height="16"><path d="M915.2 280.4l-165-8-9.6-107.6c-2-22.6-22.2-38.4-45.8-36.6L103 176.8c-23.6 2-40.6 21-38.8 43.4l42.4 471.6c2 22.6 22.4 38.4 45.8 36.6l30-2.4-4.8 91.6c-1.2 25.2 18.4 45.6 44.8 47L882.6 896c26.4 1.2 48.2-17.2 49.6-42.4L960 327c1.2-25-18.6-45.4-44.8-46.6z m-709.8 10.6l-14.2 269.6L156.2 610 124 254v-2c2-10 8.8-18 19.2-18.8l522-42.8c10.4-0.8 19.4 6 21 15.8 0 0.4 0.6 0.4 0.6 0.8 0 0.2 0.6 0.4 0.6 0.8l5.4 61.6-438-21c-26.4-0.8-48.2 17.6-49.4 42.6z m668 473.8l-169.6-199-74.8 68.6-138.4-161.6-245.4 261.4L266 336v-0.8c2-10.8 12.4-18.6 23.8-18l582.4 28c11.6 0.6 20.6 9.4 20.8 20.4 0 0.4 0.6 0.6 0.6 1l-20.2 398.2z" p-id="5311" fill="#999999"></path><path d="M768 512c35.2 0 64-28.8 64-64s-28.6-64-64-64c-35.2 0-64 28.6-64 64s28.6 64 64 64z" p-id="5312" fill="#999999"></path></svg>
          </div>`
        )
        super($elem, editor)
      }
      // 菜单点击事件
      clickHandler() {
        _this.uploadImgBox = true
      }
      tryChangeActive() {
        this.active()
      }
    }

    class WordCount extends BtnMenu {
      constructor(editor) {
          const $elem = E.$(
            `<div class="w-e-menu" data-title="字数统计">
              <svg t="1637843155098" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="9627" width="16" height="16"><path d="M445.44 527.36c10.24-10.24 20.48-15.36 35.84-25.6 10.24-10.24 20.48-15.36 30.72-25.6 10.24-5.12 20.48-15.36 30.72-25.6 10.24-10.24 15.36-20.48 20.48-30.72 5.12-10.24 10.24-25.6 10.24-46.08 0-15.36-5.12-30.72-10.24-40.96-5.12-15.36-15.36-25.6-25.6-30.72-10.24-10.24-20.48-15.36-35.84-20.48-15.36-5.12-25.6-10.24-40.96-10.24-20.48 0-35.84 5.12-51.2 10.24s-30.72 15.36-40.96 30.72c-10.24 10.24-20.48 25.6-25.6 40.96-5.12 15.36-5.12 20.48-5.12 40.96h66.56v-15.36c0-10.24 5.12-15.36 10.24-25.6 5.12-5.12 10.24-15.36 15.36-20.48 5.12-5.12 15.36-5.12 25.6-5.12 15.36 0 25.6 5.12 35.84 15.36 10.24 10.24 15.36 20.48 15.36 35.84 0 10.24 0 20.48-5.12 25.6-5.12 15.36-15.36 20.48-20.48 25.6l-20.48 20.48c-10.24 5.12-20.48 10.24-25.6 15.36-15.36 10.24-30.72 15.36-40.96 25.6-15.36 10.24-25.6 20.48-35.84 35.84-10.24 10.24-15.36 25.6-20.48 40.96-5.12 15.36-10.24 30.72-10.24 51.2h245.76v-61.44H424.96c5.12-15.36 10.24-25.6 20.48-30.72zM76.8 153.6h834.56v491.52h76.8V138.24c0-35.84-30.72-66.56-66.56-66.56H66.56C30.72 76.8 0 102.4 0 138.24v727.04c0 35.84 30.72 66.56 66.56 66.56h563.2v-76.8H76.8V153.6z m204.8 460.8V271.36h-66.56c0 10.24-5.12 25.6-10.24 35.84l-20.48 20.48c-10.24 5.12-20.48 10.24-30.72 10.24s-15.36 5.12-25.6 0v56.32h87.04v215.04h66.56z m512 0c15.36-5.12 30.72-10.24 40.96-20.48 10.24-10.24 20.48-20.48 25.6-35.84 5.12-15.36 5.12-30.72 5.12-51.2s0-30.72-10.24-46.08-25.6-20.48-46.08-25.6c15.36-5.12 25.6-15.36 35.84-25.6 10.24-10.24 10.24-25.6 10.24-40.96s-5.12-30.72-10.24-40.96c-5.12-10.24-15.36-20.48-25.6-30.72-10.24-10.24-20.48-15.36-35.84-20.48-15.36-5.12-25.6-5.12-40.96-5.12s-30.72 5.12-46.08 10.24-25.6 15.36-35.84 25.6c-10.24 10.24-20.48 20.48-25.6 35.84-5.12 15.36-10.24 35.84-10.24 51.2h66.56c0-15.36 5.12-35.84 10.24-46.08 10.24-10.24 20.48-15.36 40.96-15.36 10.24 0 25.6 5.12 30.72 10.24 10.24 5.12 15.36 20.48 15.36 30.72 0 10.24 0 15.36-5.12 20.48L768 409.6c-5.12 5.12-15.36 5.12-25.6 5.12h-25.6v46.08h30.72c10.24 0 15.36 0 25.6 5.12s15.36 10.24 20.48 15.36c5.12 5.12 5.12 15.36 5.12 30.72s-5.12 30.72-15.36 40.96c-10.24 10.24-25.6 15.36-40.96 15.36-10.24 0-20.48 0-25.6-5.12-5.12-5.12-15.36-10.24-20.48-15.36-5.12-5.12-10.24-15.36-10.24-20.48-5.12-10.24-5.12-20.48-5.12-30.72H614.4c0 20.48 0 40.96 10.24 56.32 5.12 15.36 15.36 25.6 25.6 35.84 10.24 10.24 25.6 20.48 40.96 25.6 15.36 5.12 35.84 5.12 51.2 5.12 20.48-5.12 35.84 0 51.2-5.12z m117.76 220.16V721.92h-76.8v112.64H721.92v76.8h112.64V1024h76.8V911.36H1024v-76.8H911.36z" fill="#999999" p-id="9628"></path></svg>
            </div>`
          )
          super($elem, editor)
      }
      clickHandler() {
        let editorHtml = _this['editor'+_this.getEditorType].txt.text().replace(/\s/g, "")
            editorHtml = editorHtml.replace(/&nbsp;/ig, '');//除去空格
        
        _this.$Message.warning({
          content: "字数统计："+ editorHtml.length,
          duration: 6
          })
      }
      tryChangeActive() {
        this.active()
      }
    }

    class GetHtml extends BtnMenu {
      constructor(editor) {
          const $elem = E.$(
            `<div class="w-e-menu" data-title="html代码">
              <svg t="1637893436679" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2696" width="16" height="16"><path d="M869.56139 992.952238l-0.448112 0.256064-0.768192 0.32008c-16.004001 18.692673-38.473618 30.407602-63.11978 30.407602l-586.386597 0c-24.646162 0-47.115779-11.714929-63.631908-30.727682l0-0.256064-0.256064 0.256064C138.434609 974.003501 128.384096 947.180795 128.384096 918.437609L128.384096 105.562391c0-28.807202 10.050513-55.053763 26.566642-74.002501C171.722931 12.227057 194.128532 0 218.83871 0l399.587897 0c8.898225 0 17.732433 4.737184 23.941985 11.714929l243.452863 284.67917c6.72168 8.386097 9.794449 18.180545 9.794449 28.487122l0 0.576144 0 593.044261C895.615904 947.180795 885.565391 973.427357 869.56139 992.952238zM396.803201 518.913728C408.646162 510.527632 412.615154 491.642911 405.317329 478.391598c-6.977744-14.339585-23.045761-18.884721-34.888722-10.498625L202.130533 582.865716c-15.491873 11.522881-16.068017 39.113778 0 50.700675l168.234059 115.292823c11.842961 8.386097 27.910978 4.16104 34.888722-10.114529 7.297824-13.955489 3.328832-32.136034-8.514129-40.522131L265.634409 608.088022 396.803201 518.913728zM584.498125 386.08052C571.182796 380.127032 556.523131 387.808952 551.08177 402.852713l-130.592648 389.793448c-5.185296 14.659665 1.216304 32.136034 14.275569 38.089522C448.464116 837.009252 462.995749 829.327332 468.501125 813.963491l130.592648-390.113528C603.95899 409.190298 597.237309 392.354089 584.498125 386.08052zM614.265566 478.391598C606.967742 491.642911 610.616654 510.527632 622.779695 518.913728l131.168792 89.174294-131.168792 90.19855c-12.163041 8.386097-15.811953 26.566642-8.514129 40.522131 7.297824 14.339585 23.109777 18.564641 34.952738 10.114529l167.593898-115.292823c17.348337-11.586897 16.708177-39.177794 0-50.700675L649.218305 467.892973C637.375344 459.506877 621.563391 464.052013 614.265566 478.391598zM640.384096 126.87972l0 148.709177c0 11.650913 4.993248 22.917729 12.035009 30.663666L653.315329 307.468867c8.258065 8.322081 19.716929 13.379345 32.072018 13.379345l146.852713 0L640.384096 126.87972z" p-id="2697" fill="#999999"></path></svg>
            </div>`
          )
          super($elem, editor)
      }
      clickHandler() {
        const editorHtml = _this['editor'+_this.getEditorType].txt.html();
        _this.isShowHtml = true
        _this.htmlCode = editorHtml
      }
      tryChangeActive() {
        this.active()
      }
    }

    class ClearStyle extends BtnMenu {
      constructor(editor) {
          const $elem = E.$(
            `<div class="w-e-menu" data-title="清除样式代码">
              <svg t="1637896398071" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4327" width="16" height="16"><path d="M895.398771 267.822944 671.950695 267.822944 671.950695 100.238145c0-46.261745-37.501958-83.792903-83.790889-83.792903l-167.583792 0c-46.287924 0-83.79391 37.531157-83.79391 83.792903l0 167.584799-223.445056 0c-61.698102 0-111.723535 50.024426-111.723535 111.723535l0 55.860257c0 41.398553 22.522722 77.540227 55.981082 96.841965 0.190299 56.371748-0.120825 280.654522-0.120825 377.979786 3.818059 112.787799 111.723535 111.723535 111.723535 111.723535l307.238966 0 0-0.872958 20.968111 0c2.225187 0.568883 4.557103 0.872958 6.961514 0.872958s4.736326-0.304075 6.961514-0.872958l181.59341 0c2.225187 0.568883 4.557103 0.872958 6.961514 0.872958s4.735319-0.304075 6.960507-0.872958l132.69366 0c0 0 107.902455 1.066278 111.721521-111.532229 0-97.144027-0.310117-320.985791-0.119818-377.298133 33.45836-19.301738 55.983096-55.443412 55.983096-96.842972L1007.1213 379.546479C1007.122306 317.84737 957.096873 267.822944 895.398771 267.822944zM811.604862 965.328556l-83.790889 0L727.813973 742.644695c0-15.43837-12.494276-27.929625-27.930632-27.929625-15.43837 0-27.932646 12.491255-27.932646 27.929625l0 222.683861-139.65316 0L532.297535 742.644695c0-15.43837-12.491255-27.929625-27.930632-27.929625-15.43837 0-27.929625 12.491255-27.929625 27.929625l0 223.44707c0 0-69.151976 0-139.654167 0L336.783111 742.644695c0-15.43837-12.491255-27.929625-27.930632-27.929625-15.43837 0-27.929625 12.491255-27.929625 27.929625l0 223.44707-83.79391 0c-47.459923 0-84.064758-31.259351-83.790889-83.792903 0.351398-71.180823 0.136935-263.033253 0.040275-335.168591l781.981174 0c-0.097667 72.050761-0.311123 263.506483 0.040275 334.540303C895.670627 934.122569 859.066799 965.328556 811.604862 965.328556zM951.260036 435.406736c0 30.849554-25.01171 55.860257-55.860257 55.860257L113.337047 491.266993c-30.849554 0-55.862271-25.01171-55.862271-55.860257L57.474777 379.546479c0-30.849554 25.013724-55.863278 55.862271-55.863278l279.308334 0 0-167.583792c0-46.259732 37.502965-83.792903 83.790889-83.792903l55.860257 0c46.287924 0 83.792903 37.533171 83.792903 83.792903l0 167.583792 279.308334 0c30.849554 0 55.860257 25.013724 55.860257 55.863278L951.258022 435.406736z" p-id="4328" fill="#999999"></path></svg>
            </div>`
          )
          super($elem, editor)
      }
      clickHandler() {
        let editorHtml = _this['editor'+_this.getEditorType].txt.html();
            // editorHtml = editorHtml.replace(/<\/?[^>]*>/g, '');//去除所有html标签
            editorHtml = editorHtml.replace(/(<script[^>]*>)([\s\S]*?)(<\/script>)/ig, '');
            editorHtml = editorHtml.replace(/(<style[^>]*>)([\s\S]*?)(<\/style>)/ig, '');
            editorHtml = editorHtml.replace(/<b>|<\/b>/ig, '');
            editorHtml = editorHtml.replace(/<i>|<\/i>/ig, '');
            editorHtml = editorHtml.replace(/<strike>|<\/strike>/ig, '');
            editorHtml = editorHtml.replace(/<strong>|<\/strong>/ig, '');
            editorHtml = editorHtml.replace(/<u>|<\/u>/ig, '');
            editorHtml = editorHtml.replace(/&nbsp;/ig, '');
            editorHtml = editorHtml.replace(/\s+style="[^"]*"/g, '')
            // editorHtml = editorHtml.replace(/(\n)/g, '');//去除换行
            editorHtml = editorHtml.replace(/<p[^>]*>/g, '<p>');//去除指定标签内所有属性
            editorHtml = editorHtml.replace(/<font[^>]*>/g, '<font>');
            // editorHtml = editorHtml.replace(/<img[^>]*>/g, '<img>');
            _this['editor'+_this.getEditorType].txt.html(editorHtml);
      }
      tryChangeActive() {
        this.active()
      }
    }

    class Typesetting extends BtnMenu {
      constructor(editor) {
          const $elem = E.$(
            `<div class="w-e-menu" data-title="一键排版">
              <svg t="1637904488609" class="icon" viewBox="0 0 1092 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5919" width="16" height="16"><path d="M1038.336 916.206933H53.930667A53.930667 53.930667 0 1 0 53.930667 1024h984.405333a53.930667 53.930667 0 1 0 0-107.793067zM1038.336 0H490.837333a53.930667 53.930667 0 0 0 0 107.793067h547.498667a53.930667 53.930667 0 0 0 0-107.793067z m0 215.586133H490.837333a53.930667 53.930667 0 1 0 0 107.793067h547.498667a53.930667 53.930667 0 0 0 0-107.861333z m0 269.448534H53.930667a53.930667 53.930667 0 1 0 0 107.861333h984.405333a53.930667 53.930667 0 0 0 0-107.861333z m0 215.586133H53.930667a53.930667 53.930667 0 1 0 0 107.861333h984.405333a53.930667 53.930667 0 1 0 0-107.861333zM16.861867 377.2416H103.082667l27.8528-84.036267H266.24l27.306667 84.036267h85.947733L243.3024 10.376533H150.186667L16.7936 377.2416zM149.7088 232.789333l42.325333-127.1808c2.6624-8.055467 4.573867-18.705067 5.666134-31.9488h2.184533c0.955733 10.717867 3.140267 21.026133 6.485333 30.993067l42.461867 128.136533H149.7088z" fill="#3abeb9" p-id="5920"></path></svg>
            </div>`
          )
          super($elem, editor)
      }
      clickHandler() {
        let editorHtml = _this['editor'+_this.getEditorType].txt.html();
            // editorHtml = editorHtml.replace(/(\n)/g, '');
            // editorHtml = editorHtml.replace(/\s/g, "");
            editorHtml = editorHtml.replace(/<p[^>]*>/g, '<p style="text-indent: 2em;">');//首行缩进
            editorHtml = editorHtml.replace(/<p[^>]*><img/g, '<p align="center"><img ');
            _this['editor'+_this.getEditorType].txt.html(editorHtml);
      }
      tryChangeActive() {
        this.active()
      }
    }

    //自定义菜单功能按需注册 菜单 key ，各个菜单不能重复
    this['editor'+this.getEditorType] = new E('#'+this.getEditorId)
    if(this.getEditorType == 1) {
      this['editor'+this.getEditorType].menus.extend('batchUpload', BatchUpload)
      this['editor'+this.getEditorType].config.menus = this['editor'+this.getEditorType].config.menus.concat('batchUpload')
    } else {
      _this['editor'+this.getEditorType].config.menus = [
        'bold',
        'justify',
      ]
    }
    
    // 注册图片批量上传
    // E.registerMenu('batchUpload', BatchUpload)
    // 配置菜单栏，删减菜单，调整顺序
    // 注册字数统计
    E.registerMenu('setWordCount', WordCount)
    // 注册html
    E.registerMenu('getHtml', GetHtml)
    // 注册清除样式
    E.registerMenu('clearStyle', ClearStyle)
    // 注册一键排版
    E.registerMenu('typesetting', Typesetting)
    
    //单图上传
    this['editor'+this.getEditorType].config.uploadImgMaxLength = 1 
    this['editor'+this.getEditorType].config.customUploadImg = function (resultFiles, insertImgFn) {
      // resultFiles 是 input 中选中的文件列表 insertImgFn 是获取图片 url 后，插入到编辑器的方法
      const token = getToken(),
            imgFile = resultFiles[0],
            water =  'NO';

      let formData = new FormData();
          formData.append('token',  token);
          formData.append('imgFile',imgFile);
          formData.append('water', water);
      let params = formData;
      
      api.dirImage(params).then(res => {
        if(res.code == 0) {
          const baseURl = _this.$store.state.configDomainUrl.rootMainUrl;
          let url = baseURl+res.url;
          insertImgFn(url)
        } else {
          _this.$Message.error(res.message)
        }
      })
    }
    this['editor'+this.getEditorType].create()
    this['editor'+this.getEditorType].txt.html(this.getEditorContent)
  },
  watch: {
    getEditorContent () {
      this['editor'+this.getEditorType].txt.html(this.getEditorContent)
    }
  },
  methods: {
    clearAllImg () {
      this.$refs.batchUploadImgBody.clearAllImage()
    },
    insertAllImg () {
      let urlList = this.$refs.batchUploadImgBody.waitUploadImgPreviewList,
          imgHtml = '';

      let isInsert = urlList.some(ele => { //校验是否有可插入的已上传图片
        return ele.uploaded == 1
      })

      if(urlList && urlList.length>0 && isInsert) {
        urlList.forEach(ele => {
          const baseURl = this.$store.state.configDomainUrl.rootMainUrl;
          if(ele.uploaded == 1) {//上线即可删除拼接地址
            imgHtml += `<img src="${baseURl+ele.trueUrl}">`
          }
        });
        this['editor'+this.getEditorType].cmd.do(
          'insertHTML',
          imgHtml
        );
        this.uploadImgBox = false
        this.$Message.success('全部图片插入成功')
      } else {
        this.$Message.warning('先上传图片再来试试吧')
      }
    },
    getSingleImageUrl (url) {
      const baseURl = this.$store.state.configDomainUrl.rootMainUrl;
      this['editor'+this.getEditorType].cmd.do(
        'insertHTML',//上线即可删除拼接地址
        `<img src="${baseURl+url}">`
      )
    },
    setHtml () {
      this['editor'+this.getEditorType].txt.html(this.htmlCode)
    },
    getCurEditorContent () {
      return this['editor'+this.getEditorType].txt.html()
    }
  },
  components: {BatchUploadImg}
}
</script>
<template>
  <div>
    <div :id="getEditorId" class="editorContainer"></div>
    <Modal 
      v-model="uploadImgBox" 
      draggable
      mask
      width="800">
      <p slot="header">批量图片上传<span style="font-weight:normal">（允许用户同时上传20张图片，单张图片容量不超过5MB）</span></p>
      <BatchUploadImg 
        ref="batchUploadImgBody" 
        :batchUploadImgConfig="batchUploadImgConfig" 
        @insertSingleImage='getSingleImageUrl'
        />
      <div slot="footer">
        <Button type="success" @click="insertAllImg">全部插入</Button>
